<template>
    <div>
        <c-top-back></c-top-back>
        <c-cell-wrap>
            <c-cell title="" :value='bank_code'></c-cell>
        </c-cell-wrap>
        <c-cell-wrap>
            <c-input-num
                    title=""
                    :maxvalue="maxvalue/100"
                    :words="words"
                    :val="input_money"
                    :minvalue="0.01"
                    :digit="2"
                    @on-input="getInput">
            </c-input-num>
            <c-cell
                    :title="placeholder"
                    value=''
                    @ondelete='toall'
                    rightcolor='org'
                    class='overcell'>
            </c-cell>
        </c-cell-wrap>
        <c-cell-wrap>
            <c-cell title="" :value="counter"></c-cell>
            <c-cell title="" :value='money'></c-cell>
            <c-cell title="" value='1-2'></c-cell>
        </c-cell-wrap>
        <div class="wrap">
            <c-button
                    text=""
                    :type="color"
                    :disabled="disabled"
                    @click.native="preserve"
                    size="block">
            </c-button>
            <c-button
                    text=""
                    @click.native="cancel"
                    type="gray"
                    size="block"
                    style='color: #7D7D7D;background-color: #E6E6E6;'>
            </c-button>
        </div>
        <r-popup v-model="cash_result" height="100%">
            <div class="popup1">
                <c-msg :status="cash_status" :msg="cash_title">
                    <div slot="btn">
                        <c-button
                                :text="cash_text"
                                type="primary"
                                size="block"
                                @click.native="btnClick"
                                style='background-color: #5ABA5A;'>
                        </c-button>
                    </div>
                </c-msg>
            </div>
        </r-popup>
    </div>
</template>
<script>
    import utils from '../../libs/utils.js';
    export default {
        data (){
            return {
                color: 'default',                        // 
                disabled: true,                       	 // 
                bank_code: '',                           // 
                data_counter: 3,                         // 
                data_money: 0,                           // 
                counter: '3.00',                        //  
                money: '0.00',                          //  
                input_money: null,                      // 
                maxvalue: 0,                            // 
                cash_result: false,
                cash_text: '',
                cash_title: '',
                cash_status: 0,
                words: {
                    error: '',
                    min: '100',
                    max: '',
                    empty: '',
                },

            }
        },
        mounted: function () {
            this.$nextTick(function () {
//				utils.MenuShare();
                let that = this;
                utils.ajax({
                    url: "/seller/account/getStoreBankCard",   //
                    dataType: 'json',
                    type: 'POST',
                    data: {},
                    success: function (data) {
                        if (data.code == 'SUCCESS') {
                            that.bank_code = data.result.card_no;
                        } else {
                            that.$vux.alert.show(data.message);
                        }
                    }
                });
                utils.ajax({
                    url: "/seller/account/getAccountAmount",   //
                    dataType: 'json',
                    type: 'POST',
                    data: {},
                    success: function (data) {
                        if (data.code == 'SUCCESS') {
                            that.maxvalue = data.result.withdrawals_amount;
                        } else {
                            that.$vux.alert.show(data.message);
                        }
                    }
                });
            })
        },
        methods: {
            cancel(){
                //
                utils.go({path: '/tx/balance', query: {}}, this.$router);   //
            },
            tocash(that){
                utils.ajax({
                    url: "/seller/account/withDrawals",
                    dataType: 'json',
                    type: 'POST',
                    data: {
                        'amount': that.input_money * 100,              //    
                        'commission': 1 || that.data_counter * 100,        //    
                    },
                    success: function (data) {
                        that.cash_result = true;
                        if (data.code == 'SUCCESS') {
                            that.cash_text = '';
                            that.cash_title = '';
                            that.cash_status = 1;
                        } else {
                            that.cash_text = '';
                            that.cash_title = data.message;
                            that.cash_status = 0;
                        }
                    }
                });
            },
            preserve(){    // 
                let that = this;
                that.tocash(that);
            },
            getInput(v, s){
                let that = this;
                if (s == 'success') {
                    that.input_money = v;
                    that.statusCtrl(v, that);
                } else {
                    that.disabled = true;
                    that.color = 'default';
                }

            },
            toall(){   //
                let that = this;
                that.input_money = that.maxvalue / 100;
                $('[data_id=inputNum]').val(that.maxvalue / 100);
                that.statusCtrl(that.maxvalue, that);
            },
            btnClick(){
                if (this.cash_text == '') {
                    utils.go({path: '/tx/cashrecord', query: {}}, this.$router); //    
                } else {
                    utils.go({path: '/tx/balance', query: {}}, this.$router);   //    
                }

            },
            statusCtrl(v, that){   // 
                that.disabled = false;
                if (that.input_money > 0) {
                    that.color = 'primary';
                    if (that.input_money > 100) {
                        that.data_money = that.input_money - that.input_money * 0.006 - 3;
                        that.money = utils.formatPrice(that.data_money * 100) + '';

                        that.data_counter = 3 + that.input_money * 0.006;
                        that.counter = utils.formatPrice(that.data_counter * 100) + '';
                    } else {
                        that.data_money = that.input_money - 3;
                        that.money = utils.formatPrice(that.data_money * 100) + '';

                        that.data_counter = 3;
                        that.counter = utils.formatPrice(that.data_counter * 100) + '';
                    }
                } else {
                    that.color = 'default';
                    that.disabled = true;
                }
            }
        },
        computed: {
            placeholder: function () {
                return " " + this.maxvalue / 100;
            }
        },
        components: {
            "cGroup": require('../../components/group/group.vue'),
            "cTopBack": require('../../components/x-top-back/x-top-back.vue'),
            "cButton": require('../../components/button/button.vue'),
            "cCellWrap": require('../../components/x-cell/cell-wrap.vue'),
            "cCell": require('../../components/x-cell/cell.vue'),
            "cInputNum": require('../../components/x-withdraw/x-withdraw.vue'),
            "cMsg": require('../../components/x-messages/x-messages.vue'),
            "rPopup": require('../../components/popup/popup.vue')
        }
    }
</script>
<style lang="less" scoped>
    .popup1 .weui_btn_primary:active {
        background-color: #5ABA5A !important;
        color: #fff !important;
    }
</style>





